<style lang=less>
    @import '../../../assets/index.less';
    @import './commission.less';
</style>
<template>
    <div class="base-info commission">
        <!-- 基本信息 -->
        <p class="title">
            <span>
            </span>
            {{$t('center.commission[0]')}}
        </p>
        <el-table :data="tableData" style="width: 100%;min-height: 580px;">
            <el-table-column prop="username_down" :label="$t('center.commission[1]')" width="180">
            </el-table-column>
            <el-table-column prop="fee_yongjin" :label="$t('center.commission[2]')" width="180">
            </el-table-column>
            <el-table-column prop="add_time" :label="$t('center.commission[3]')" align="right">
                    <template slot-scope="scope">
                            {{$public.timestampToTime(scope.row.add_time)}}
                    </template>
            </el-table-column>
        </el-table>
        <pagination :page="paginationdata" @change="getValueFromChild"></pagination>
    </div>
</template>
<script>
    import  pagination from "@/components/pagination";
  import {reward_detail} from "@/api/user.js";
export default {
    data () {
        return {
           tableData: [],
            paginationdata:{
                total: 10,
                p: 1,
            }
        }
    },
    created () {
        this.getdata('1');
    },
    watch: {
    },
    methods: {
        getdata(page){
            let _this = this;
            reward_detail({p:page,size:"15"}).then(res=>{
                _this.tableData = res.data.data.list;
                _this.paginationdata.total = res.data.data.count;
                _this.paginationdata.p = res.data.data.p;
            });
        },
        getValueFromChild(value1){
            let _this = this;
            _this.getdata(value1)
        }
    },
    components: {
        pagination,
    },
}
</script>



